body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Raleway', serif;
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box; }

* {
  box-sizing: border-box; }

#layout-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  scroll-behavior: smooth;
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory; }

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100vh;
  padding: 0;
  scroll-snap-align: center; }

.navbar {
  position: fixed;
  left: 0;
  z-index: 1;
  width: 60px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }
  .navbar ul.bubbles {
    list-style: none;
    height: auto;
    justify-content: center;
    padding: 0; }
    .navbar ul.bubbles li {
      font-size: 1rem;
      margin: 1em;
      padding: 0;
      border-radius: 2.5em;
      overflow: wrap; }
      .navbar ul.bubbles li a {
        height: 2.5em;
        width: 2.5em;
        margin: 0;
        padding: 0;
        line-height: 2.5em;
        padding-left: 20px;
        padding-right: 20px;
        text-decoration: none;
        text-transform: uppercase;
        display: inline-block;
        color: transparent;
        border-radius: inherit;
        overflow: hidden;
        background: rgba(0, 0, 0, 0.4);
        -webkit-transition: color .5s ease, background .5s ease;
        transition: color .5s ease, background .5s ease; }
        .navbar ul.bubbles li a:hover {
          color: white;
          background: rgba(0, 0, 0, 0.8);
          -webkit-transition: color .3s ease, background .3s ease;
          transition: color .3s ease, background .3s ease; }

.Introduction {
  box-sizing: border-box;
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
  padding: 10px;
  background-color: #333;
  color: #EFEFEF;
  text-shadow: 0 2px 10px #121212; }
  .Introduction a {
    color: inherit; }
  .Introduction .hoverable {
    text-shadow: 0 2px 10px #121212;
    -webkit-transition: text-shadow .5s ease;
    transition: text-shadow .5s ease; }
  .Introduction .hoverable:hover {
    text-shadow: 0 2px 30px black;
    -webkit-transition: text-shadow .3s ease;
    transition: text-shadow .3s ease; }
  .Introduction .clickable {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: color .5s ease, text-shadow .5s ease;
    transition: color .5s ease, text-shadow .5s ease; }
  .Introduction .clickable:hover {
    color: #58A8D5;
    text-shadow: 0 0 30px #58A8D5;
    -webkit-transition: color .3s ease, text-shadow .3s ease;
    transition: color .3s ease, text-shadow .3s ease; }
  .Introduction .Introduction-container {
    display: table-cell;
    vertical-align: middle; }
    .Introduction .Introduction-container .info {
      padding: 0 30px;
      font-size: 1em; }
      .Introduction .Introduction-container .info .long-name {
        display: inline-block; }
        @media screen and (max-width: 520px) {
          .Introduction .Introduction-container .info .long-name {
            display: none; } }
      .Introduction .Introduction-container .info .short-name {
        display: none; }
        @media screen and (max-width: 520px) {
          .Introduction .Introduction-container .info .short-name {
            display: inline-block; } }
      .Introduction .Introduction-container .info div {
        margin: 20px 50px; }
      .Introduction .Introduction-container .info .name, .Introduction .Introduction-container .info .last-name {
        font-family: 'Libre Baskerville', serif; }
      .Introduction .Introduction-container .info .name {
        font-size: 4em; }
        .Introduction .Introduction-container .info .name .last-name {
          color: #58A8D5; }
      .Introduction .Introduction-container .info .desc {
        font-size: 1em;
        color: #bcbcbc;
        margin-bottom: 50px; }
      .Introduction .Introduction-container .info .links {
        font-size: 1.8em; }
        .Introduction .Introduction-container .info .links a {
          margin-left: 10px;
          margin-right: 10px; }

.Academic {
  width: 100%;
  height: 100%;
  padding: 40px;
  box-sizing: border-box;
  background: #EFEFEF; }
  .Academic h1 {
    margin-bottom: 30px; }
  .Academic ul.historic {
    list-style: none;
    text-align: left;
    overflow: auto; }
    .Academic ul.historic li {
      margin-top: 1em;
      overflow: auto;
      min-width: 370px; }
    .Academic ul.historic .years {
      display: block;
